html{
  overflow-x: hidden;
}
/* textForm.js css */
*{
  font-family: monospace ;
}
.form{
  display: flex;
  flex-direction: column;
  justify-content: left;
  text-align: left;
  align-items: center;
  width: 100vw;
  margin: 20px 0;
}
#textarea , .summaryPara{
  width: 80vw;
  font-size:20px ;
  letter-spacing: 2px;
  margin-bottom: 20px ;
}
.form h1{
  font-family: 'Akaya Kanadaka';
  margin: 15px;
  width: 80vw;
  text-align: left; 
}
.btn_container {
  width: 80vw;
  display: flex;
  justify-content: space-around; 
  text-align: center;
  align-items: left;
}
.btn{
  background-color: #FF5200;
  color: white;
  padding: 8px;
  font-size: 18px;
  font-family: 'Merienda', cursive;
  font-weight: bolder;
  border: 4px groove #e44901;
  border-radius: 4px;
  
  
  
}

/* ------------------------------------------------------------------------------------ */
/* About.js  css */
.aboutDiv{
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  align-items: center;
  margin: 20px 10vw ;
  width: 80vw;
}

.aboutDiv h1{
  margin: 10px;
  width: 80vw;
  text-align: left;
  font-family: 'Akaya Kanadaka';
  
}
.about_para{
  margin: 10px;
  width: 80vw;
  font-family: monospace;
  font-size: 17px;
  font-weight: 400;
}
#creator_name_para{
  letter-spacing: 2px;
  margin: 10px;
  width: 80vw;
  font-weight: bolder;
  font-family: 'Merienda',cursive;
  font-size: 22px;
}


/* ----------------------------------------------------------------------------------- */
/*  alert.js css  */

#alert{
  background: lightgreen;
  display: block;
  width: 100vw;
  padding: 20px;
  font-size: 18px;
  font-family: monospace;
  font-weight: 500;
}
#alert_parent{
  height: 6vh;
  /* background-color: red; */
}


*{
  margin: 0;
  padding: 0;
}
.navbar ,.logo_img_div  {
  display: flex;
  justify-content: left;
  align-items: center;
  text-align: center;
}
.logo_img_div p {
  font-size: 24px;
  margin-right: 10px;
  font-weight: bolder;
  font-family: 'Akaya Kanadaka' , "Merienda",cursive;
}
#logo_img{
  margin: 1px 5px;
  height: 10vh;
  width: 5vw;
  border-radius: 20px;
}
.links{
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  width: 42vw;
  margin: 0 3vw;
}
.links a {
  color: #000;
  font-family: 'Viaoda Libre', cursive;
  text-decoration: none;
  font-weight: bolder;
  font-size: 22px;
  
}
.searchBox{ 

  border: 4px groove rgb(111, 214, 248);
  border-radius: 10px;
  
}
#search_input{
  width: 17vw;
  border: none;
  background: none;
  outline: none;
  padding: 4px; 
  font-size: 20px;
  font-family: monospace, cursive;
  font-weight: bolder;
}
#search_btn{
  border: 2px solid rgb(19, 186, 241);
  background: none;
  outline: none;
  padding: 5px;
  font-size: 20px;
  font-family: 'Akaya Kanadaka', cursive;
  font-weight: bolder; 
  border-radius: 7px;
}

#search_btn:hover{
  padding: 7px;
  background: rgb(19, 186, 241);
  border: none;
} 


nav #drak_light_switch{
  background-color: #FF5200;
  color: white;
  padding: 4px;
  margin: 0 20px;
  font-size: 16px;
  font-family: 'Merienda', cursive;
  font-weight: bolder;
  border: 2px groove #e44901;
  border-radius: 6px;
}

